html {
    color: white;
    background-color: black;
    font-size: 1.25rem;
}
.container {
    display: grid;
    height: 100%;
}
@media (min-width: 900px) {
    .container {
        grid-template-columns: 1fr 1fr 1fr;
    }
}
.main {
    text-align: center;
    grid-column: 2 / 3;
    align-self: start;
    font-family: monaco, monospace;
}
.line {
    width: 100%;
    height: 1px;
    background-color: white;
    margin-top: 20px;
    margin-bottom: 20px;
}
li {
    text-align: left;
    padding: 10px;
}
b {
    text-decoration: underline;
}
.buy {
    padding: 10px;
    margin: 10px;
    color: white;
    background-color: black;
    border-radius: 0;
    border-width: 2px;
    border-color: white;
    font: inherit;
}
